<template>
  <div class="DetailView w-full h-full overflow-hidden">
    <TopMenu ref="TopMenu"></TopMenu>
    <div
      class="arrow w-10 h-10 absolute top-28 left-16 cursor-pointer"
      @click="$router.go(-1)"
    >
      <img src="resources/icon/back.png" class="object-contain" alt="" />
    </div>
    <div
      class="box flex justify-center p-24 text-2xl w-full h-full items-center text-white"
    >
      <div class="flex flex-col">
        <div class="video_part">
          <video
            src="resources/projects/clip/1.mp4"
            class="w-full h-full"
            muted
            controls
          ></video>
        </div>
        <div class="content pt-5 font-bold">
          该作品是为科勒极致淋浴体验视频加的创意中文字幕。 <br />
          该视频是在品牌原视频的基础上，添加与其原生的英文<br />
          字幕相匹配的中文字幕。使用了淡入淡出等多重效果,<br />以及进行卡点计算，
          做出与原视频协调一致的中文版字幕视频。
        </div>
      </div>
      <div class="flex flex-col ml-24 mb-16 items-center">
        <div class="video_part">
          <video
            src="resources/projects/clip/2.mp4"
            class="w-full h-full"
            muted
            controls
          ></video>
        </div>
        <div class="content pt-5 font-bold">
          这是为施乐辉公司制作的PC端上线短视频。
          剪辑中主要使用AE，制作电脑展示的特殊效果，视频背景也做了微小
          的移动，给人一种灵动的立体感。
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import TopMenu from "@/components/TopMenu.vue";

export default {
  components: {
    TopMenu,
  },
  // props: ["imgList"],
  data() {
    return {
      descContent: {},
    };
  },
  mounted() {
    console.log(this.$route);
    this.updateData(Number(this.$route.params.id));
    this.changeTopMenu();
  },
  methods: {
    changeTopMenu() {
      let topMenu = this.$refs.TopMenu.$refs.topItem;
      // 监听滚动事件
      window.addEventListener("scroll", function () {
        // 页面可视区域的高度
        const windowHeight = window.innerHeight;
        // 整个页面的高度（包括滚动部分）
        const documentHeight = document.documentElement.scrollHeight;
        // 当前滚动位置
        const scrollPosition = window.scrollY;
        // 如果滚动到页面底部
        if (scrollPosition + windowHeight >= documentHeight) {
          // 修改背景颜色为黑色
          topMenu.style.transition = `0.5s`;
          topMenu.style.backgroundColor = "transparent";
        } else {
          // 否则背景颜色为透明
          topMenu.style.transition = `0.5s`;
          topMenu.style.backgroundColor = "transparent";
        }
      });
    },
    updateData(index) {
      switch (index) {
        case 1:
          this.imgList = { ...this.metaList };
          this.descContent = { ...this.metaDesc };
          break;
        case 2:
          this.imgList = { ...this.xiaList };
          this.descContent = { ...this.xiaDesc };
          break;
        case 3:
          this.imgList = { ...this.bookList };
          this.descContent = { ...this.bookDesc };
          break;
        case 4:
          this.imgList = { ...this.seasonList };
          this.descContent = { ...this.seasonDesc };
          break;
        case 5:
          this.imgList = { ...this.leifengList };
          this.descContent = { ...this.leifengDesc };
          break;
        case 6:
          this.imgList = { ...this.springList };
          this.descContent = { ...this.springDesc };
          break;
        case 7:
          this.imgList = { ...this.dreamList };
          this.descContent = { ...this.dreamDesc };
          break;
      }
    },
  },
};
</script>

